
import React, { Component } from 'react'
import Link from 'next/link'
import Web3Container from '../lib/Web3Container'

class Details extends Component {
    state = {web3: null, contract: null, accounts: null}
    
    // static async getInitialProps(props) {  
    //     const { web3, contract, accounts } = props.query  
    
    //     console.log(web3)
    //     console.log(contract)    
    //     console.log(accounts)

    //     return  { web3, contract, accounts }
    // }

    async componentDidMount () {
        const { web3, contract, accounts } = this.props
    
        console.log(web3)
        console.log(contract)    
        console.log(accounts) 

        const name = await contract.methods.name().call();
        const symbol = await contract.methods.symbol().call();
        const totalSupply = await contract.methods.totalSupply().call();
        const currentAccount = accounts[0];
        const accountBalance = await contract.methods.balanceOf(currentAccount).call();
        const allowance = await contract.methods.allowance(currentAccount, accounts[1]).call();

        this.setState({ 
            web3, 
            contract, 
            accounts, 
            name, 
            symbol, 
            totalSupply, 
            currentAccount, 
            accountBalance,
            allowance
        }); 
    }

    render() {
         
        return (
            <div>
                <div>{this.state.name}</div>
                <div>{this.state.symbol}</div>
                <div>{this.state.totalSupply}</div>
                <div>{this.state.currentAccount}</div>
                <div>{this.state.accountBalance}</div>
                <div>{this.state.allowance}</div>
            </div>
        )
    }    
}

export default () => (
    <Web3Container
      renderLoading={() => <div>Loading Dapp Page...</div>}
      render={({ web3, accounts, contract }) => (
        <Details accounts={accounts} contract={contract} web3={web3} />
      )}
    />
)


